import Bowser from 'bowser/bundled';
import Emitter from 'component-emitter';
import jss from 'jss-browserify';
import WebFont from 'webfontloader';
import semver from 'semver-compare';
import deepAssign from 'recurly.js/lib/util/deep-assign';
import dom from 'recurly.js/lib/util/dom';
import wrapElement from '../util/wrap-element';
import { Config } from '../config';
import { Message } from 'recurly.js/lib/recurly/message';
import { PermissiveBus, SenderBus } from '../bus';
import { version } from '../version';

const debug = require('debug')('hostedField:field');

const STYLE_PROPERTIES = [
  'fontFamily',
  'fontFeatureSettings',
  'fontKerning',
  'fontSize',
  'fontStretch',
  'fontStyle',
  'fontVariant',
  'fontWeight',
  'letterSpacing',
  'lineHeight',
  'padding',
  'textAlign',
  'textDecoration',
  'textRendering',
  'textShadow',
  'textTransform'
];

const WEB_SAFE_FONTS = [
  'sans-serif',
  'arial',
  'arial black',
  'calibri',
  'geneva',
  'tahoma',
  'trebuchet ms',
  'verdana',

  'serif',
  'consolas',
  'courier',
  'courier new',
  'georgia',
  'times',
  'times new roman'
];

export class Field extends Emitter {
  static bowser = Bowser.getParser(window.navigator.userAgent);

  constructor (element) {
    super();

    this.style = this.style.bind(this);
    this.onReady = this.onReady.bind(this);
    this.onBlur = this.onBlur.bind(this);
    this.onFocus = this.onFocus.bind(this);
    this.onInput = this.onInput.bind(this);

    this.version = version;
    this.config = new Config;

    this.loadedFonts = [];

    this.inject(element);

    const { busGroupId: groupId } = this.config.config;

    this.bus = new PermissiveBus({ groupId, role: 'hosted-field' });
    this.bus.add(this);
    this.bus.add(this.config);

    this.parentBus = new SenderBus({ groupId, role: 'hosted-field-parent' });
    this.parentBus.add(window.parent);

    this.style();
    this.tabbing();
    this.bind();
    this.update();

    this.sendMessage('ready', this.state);
  }

  get type () {
    return this.config.config.type;
  }

  get value () {
    return this.el.value;
  }

  get state () {
    return this._state = this._state || {};
  }

  set state (changes) {
    const oldState = this.state;
    try {
      const newState = deepAssign({}, this.state, changes);
      if (JSON.stringify(oldState) === JSON.stringify(newState)) return;
      this._state = newState;
    } catch (e) {
      debug('Failed to determine state change', oldState, changes);
    }
    this.sendMessage('state:change', this.state);
  }

  get shouldFormat () {
    return this.config.config.format === true;
  }

  get inputEl () {
    return this.el;
  }

  get shouldLoadWebFont () {
    const fontFamily = this.config.config.style.fontFamily;
    const alreadyLoadedFont = ~this.loadedFonts.indexOf(fontFamily);

    if (fontFamily === this.config.defaults.style.fontFamily || alreadyLoadedFont) return false;

    // Detect if all of the specified fonts are web safe
    // '"Source Sans Pro", "Times New Roman", serif' -> ['source sans pro', 'times new roman', 'serif']
    const parsedFontFamily = fontFamily.split(',').map(font => font.trim().replace(/^"(.*)"$/, '$1').toLowerCase());
    if (parsedFontFamily.filter(font => !~WEB_SAFE_FONTS.indexOf(font)).length === 0) return false;

    return true;
  }

  get isElement () {
    return !!this.elementId;
  }

  get elementId () {
    return  this.config.config.elementId;
  }

  get isMobile () {
    return Field.bowser.is('mobile') || Field.bowser.is('tablet');
  }

  get isIos () {
    return Field.bowser.is('ios');
  }

  bind () {
    this.config.on('change', this.style);

    this.el.addEventListener('keyup', this.onInput);
    this.el.addEventListener('focus', this.onFocus);
    this.form.addEventListener('submit', e => e.preventDefault());
    window.addEventListener('blur', this.onBlur);

    // Bind focus events from container elements to the input field
    const focus = this.el.focus.bind(this.el);
    window.document.addEventListener('focus', focus);
    window.document.body.addEventListener('focus', focus);

    if (this.isElement) {
      this.on(`element:${this.elementId}:focus!`, focus);
      this.on('elements:ready!', this.onReady);
    } else {
      this.on(`hostedField:${this.type}:focus!`, focus);
      this.on('hostedFields:ready', this.onReady);
    }
  }

  inject (element) {
    this.el = element;
    this.el.setAttribute('type', 'tel');
    this.el.setAttribute('pattern', '[0-9]*');
    this.el.setAttribute('spellcheck', 'false');
    this.el.setAttribute('autocapitalize', 'none');
    this.el.setAttribute('autocorrect', 'off');
    this.el.className = 'recurly-hosted-field-input';
    this.el.style.visibility = 'hidden';
    this.form = wrapElement(this.el, 'form');
    this.form.setAttribute('novalidate', 'true');
    this.form.style.width = '100%';
    this.form.style.height = '100%';
    this.form.style.margin = '0';
    this.form.style.padding = '0';
  }

  style () {
    const style = this.config.config.style;

    // Backward compatibility
    if (typeof style.placeholder === 'string') {
      style.placeholder = { content: style.placeholder };
    }

    if (this.shouldLoadWebFont) {
      WebFont.load({
        google: {
          families: googleFontFamilies(style.fontFamily)
        },
        fontinactive: debug
      });

      this.loadedFonts.push(style.fontFamily);
    }

    this.applyStyleToElement();

    // Placeholder styles
    this.el.placeholder = style.placeholder.content;
    [
      'input::-webkit-input-placeholder',
      'input::-moz-placeholder',
      'input:-ms-input-placeholder'
    ].forEach(sel => {
      // Chrome & FF choke on other vendor prefixes
      try {
        let placeHolderStyle = Object.assign({}, style.placeholder, { content: '' });

        // IE11 will not render a placeholder color without the !important flag
        if (placeHolderStyle.color && !~placeHolderStyle.color.indexOf('!important')) {
          placeHolderStyle.color += ' !important';
        }

        jss.remove(sel);
        jss.set(sel, placeHolderStyle);
        // eslint-disable-next-line
      } catch (e) {}
    });

    // Accessibility
    const title = style.title || style.placeholder.content;
    if (title) {
      this.el.setAttribute('title', title);
      this.el.setAttribute('aria-label', title);
    }
  }

  /**
   * Builds hidden tabbing sinks for mobile browsers
   *
   * The previous and next tabbing sinks will only be tabbable when the main element is focused,
   * preventing them from being focusable from outside the field
   */
  tabbing () {
    // Compatible only with Recurly.js 4.8.3+
    if (this.parentRecurlyJSVersionIsLessThan('4.8.3')) return;
    if (!this.isMobile) return;

    const previous = dom.createHiddenInput({ 'data-recurly-tabbing': 'prev' });
    const next = dom.createHiddenInput({ 'data-recurly-tabbing': 'next' });

    previous.addEventListener('focus', () => this.sendMessage('tab:previous'));
    next.addEventListener('focus', () => this.sendMessage('tab:next'));

    this.inputEl.parentNode.insertBefore(previous, this.inputEl);
    this.inputEl.parentNode.insertBefore(next, this.inputEl.nextSibling);
  }

  onReady () {
    this.el.style.visibility = 'visible';
  }

  update () {
    const empty = this.value === '';
    const length = this.value.length;
    const focus = this.isFocused();

    this.state = { empty, length, focus };
  }

  /**
   * Event handlers
   */

  onInput (event) {
    if (event.which === 13) this.sendMessage('submit'); // Enter
    this.update();
  }

  onFocus () {
    this.update();
    this.sendMessage('focus');
  }

  onBlur () {
    this.update();
    this.forceIosBlur();
    window.getSelection().removeAllRanges();
    this.sendMessage('blur');
  }

  /**
   * A bug in iOS prevents input elements from blurring when moving focus
   * between iframe and parent. This creates a button element upon which we
   * force a focus and blur.
   *
   * @protected
   */
  forceIosBlur () {
    if (!this.isIos) return;
    // This excludes blurs that occur due to tabbing
    if (window.document.activeElement === window.document.body) return;
    if (!this.iOSBlurFixElement) {
      this.iOSBlurFixElement = dom.createHiddenInput({ type: 'button' });
      window.document.body.appendChild(this.iOSBlurFixElement);
    }
    this.iOSBlurFixElement.focus();
    this.iOSBlurFixElement.blur();
  }

  /**
   * Determines if the given element is currently focused
   *
   * Compensates for a bug in IE browsers that throws an 'unspecified error'
   * when the activeElement cannot be found within an iframe
   *
   * @param {HTMLElement} el element to test for focus
   * @return {boolean} whether the given element is focused
   */
  isFocused (el = this.el) {
    let focus = false;
    try {
      const isActive = el === window.document.activeElement;
      const hasFocus = window.document.hasFocus();
      focus = hasFocus && isActive;
    } catch (e) {
      debug('error retrieving activeElement');
    }
    return focus;
  }

  /**
   * Sends a message intended for a parent window
   *
   * @param {String} name
   * @param {Object} body
   */
  sendMessage (name, body = {}, { overrideName = false } = {}) {
    let messageName;

    if (overrideName) {
      messageName = name;
    } else {
      messageName = this.isElement ? `element:${this.elementId}:${name}` : `hostedField:${name}`;
    }

    const messageBody = deepAssign({}, body, { type: this.config.config.type });

    this.parentBus.send(new Message(messageName, messageBody, this.bus.groupId));
  }

  /**
   * Applies configured style properties to el
   *
   * @param {HTMLElement} [el=this.el]
   */
  applyStyleToElement (el = this.el) {
    const style = this.config.config.style;

    el.style.color = style.fontColor;
    STYLE_PROPERTIES.forEach(prop => el.style[prop] = style[prop]);
    el.style.fontFamily = cssFontFamily(style.fontFamily);

    // Font smoothing
    if (style.fontSmoothing === 'off') {
      el.style.webkitFontSmoothing = 'none';
      el.style.MozOsxFontSmoothing = 'unset';
    } else if (style.fontSmoothing === 'antialiased') {
      el.style.webkitFontSmoothing = 'antialiased';
      el.style.MozOsxFontSmoothing = 'grayscale';
    }
  }

  /**
   * Checks the Recurly.js version for compatibility purposes
   *
   * @param {String} test minimum version ex: '4.8.0'
   */
  parentRecurlyJSVersionIsLessThan (test) {
    return semver(this.config.config.recurly.parentVersion || '0.0.0', test) < 0;
  }
}

/**
 * Formats a fontFamily string for the Google Web Font API
 * @param  {String} fontFamily '"Droid Sans", Helvetica, sans-serif'
 * @return {Array}             ['Droid Sans', 'Helvetica']
 */
function googleFontFamilies (fontFamily) {
  return fontFamily
    .split(',')
    .map(f => f.trim().replace(/'|"/g, ''))
    .filter(f => !~WEB_SAFE_FONTS.indexOf(f));
}

/**
 * Creates a valid fontFamily cstyle attribute
 * - strips away font sizing specifications for a Google Font, to be
 *
 * @param  {String} fontFamily '"Montserrat:100", Helvetica, sans-serif'
 * @return {String}            '"Montserrat", Helvetica, sans-serif'
 */
function cssFontFamily (fontFamily) {
  return fontFamily.split(',').map(f => f.trim().split(':')[0]).join(', ');
}
