import { NumberField } from './number';
import { MonthField } from './month';
import { YearField } from './year';
import { CVVField } from './cvv';
import { CardField } from './card';

export const FIELD_TYPES = {
  number: NumberField,
  month: MonthField,
  year: YearField,
  cvv: CVVField,
  card: CardField
};

/**
 * Returns a memoized Field instance
 *
 * @param  {String} type
 * @param  {HTMLElement} input
 * @param  {Window} global
 * @return {Field}
 */
export default function factory (type, input) {
  if (!(type in FIELD_TYPES)) return error(`'${type}' is not a valid field type`);
  if (!(input instanceof HTMLElement)) return error('input is not an HTMLElement');
  return new FIELD_TYPES[type](input);
}

function error (message) {
  console.error('There was an error initializing this hosted field', message);
}
