import Payment from 'payment';

/**
 * Restricts a field to numeric input, optionally with a max length
 * @param {Object} options
 * @param {Number} [maxLength] maximum length of input
 */
export const StrictNumeric = (superclass, opts = {}) => class extends superclass {
  constructor (...args) {
    super(...args);

    if (typeof opts.maxLength === 'number' && this.shouldFormat) {
      this.el.setAttribute('maxlength', opts.maxLength);
    }
  }

  bind (...args) {
    super.bind(...args);
    if (this.shouldFormat) Payment.restrictNumeric(this.el);
  }
};
