import Payment from 'payment';
import { Field } from './field';

export class CVVField extends Field {
  constructor (...args) {
    super(...args);
    this.el.setAttribute('autocomplete', 'off');
    if (this.shouldFormat) Payment.formatCardCVC(this.el);
  }

  update () {
    const valid = Payment.fns.validateCardCVC(this.value);
    this.state = { valid };
    super.update();
  }
}
