import recurlySingleton from 'recurly.js';

const debug = require('debug')('hostedField:tokenizing');

/**
 * Allows a Field to create tokens
 *
 * Events
 *   Listens to
 *     - `token:init` and will create a token from the message body
 *   Emits
 *     - `token:done:${id}` when the token is created
 *
 * @typedef {TokenInitMessageBody}
 * @property {String} id     tokenization identifier. To be echoed in
 *                           `token:done:${id}` message
 * @property {Object} inputs tokenization
 */
export const Tokenizing = (superclass) => class extends superclass {
  constructor (...args) {
    super(...args);

    this.recurly = this.createRecurly();

    this.on('token:init', body => this.token(body));
  }

  /**
   * Forms a tokenization payload from a `token:init` message body
   *
   * @param  {TokenizationMessageBody} body
   * @return {Object}                  payload to submit to `recurly.token`
   *
   */
  tokenPayload (body) {
    return body;
  }

  /**
   * Handles `token:init` messages
   *
   * @param  {TokenInitMessageBody} body
   */
  token (body) {
    const { id } = body;
    const { recurly } = this;
    const payload = this.tokenPayload(body);

    debug('received token:init', body);

    recurly.token(payload, (err, token) => {
      const name = `token:done${id ? `:${id}` : ''}`;
      this.sendMessage(name, { err, token }, { overrideName: true });
    });
  }

  /**
   * Creates a Recurly instance on which we may tokenize
   *
   * @return {Recurly}
   */
  createRecurly () {
    const { Recurly } = recurlySingleton;
    const { config } = this.config;
    const { deviceId, sessionId } = config;

    const recurly = this.recurly = new Recurly;

    Object.defineProperty(recurly, 'deviceId', { get: () => deviceId });
    Object.defineProperty(recurly, 'sessionId', { get: () => sessionId });

    recurly.configure(Object.assign({}, config.recurly, { parent: false }));

    // HACK: `bus` is assigned here because downstream tokenization preflight routines must
    // have access to an active bus.
    recurly.bus = this.bus;

    return recurly;
  }
};
