/**
 * Wraps an HTMLElement in-place
 *
 * @param  {HTMLElement} el
 * @return {DivElement}
 */
export default function wrapElement (el, type = 'div') {
  const container = window.document.createElement(type);
  el.parentNode.insertBefore(container, el);
  container.appendChild(el);
  return container;
}
