import createNativeEvent from '../../util/create-native-event';

const debug = require('debug')('hostedField:connected');

export const Connected = (superclass) => class extends superclass {
  constructor (...args) {
    super(...args);

    const onReady = () => this.clientsReady = true;

    if (this.isElement) {
      this.clientElementIds = [];
      this.on('elements:peer-announce', (...args) => this.onPeerAnnounce(...args));
      this.on('elements:ready!', onReady);
    } else {
      this.on('hostedFields:ready', onReady);
    }
  }

  get clients () {
    const { clientsReady, clientElementIds, isElement } = this;
    if (!clientsReady) return [];
    if (isElement) {
      return this.getClientByMatcher(frame => clientElementIds.some(id => ~frame.name.indexOf(id)));
    } else {
      return this.getClientByMatcher(frame => ~frame.name.indexOf('recurlyHostedField'));
    }
  }

  /**
   * Finds a client field by its type
   *
   * @protected
   * @param  {String} type
   * @return {Field}
   */
  getClientByType (type) {
    const { clients } = this;
    for (let i = 0, len = clients.length; i < len; i++) {
      if (clients[i].type === type) return clients[i];
    }
  }

  /**
   * Inspects parent frames and returns those which match according to a matcher function
   *
   * @private
   * @param {Function} matcher Signature: `matcher(frame)`, expects {Boolean} return
   * @return {Array[Field]}
   */
  getClientByMatcher (matcher) {
    const frames = window.parent.frames;
    const loc = window.location;
    const origin = `${loc.protocol}//${loc.host}`;

    let clients = [];
    for (let i = 0; i < frames.length; i++) {
      let frame = frames[i];
      try {
        if (~frame.location.href.indexOf(origin) && matcher(frame)) {
          clients.push(frame.recurlyHostedField);
        }
      } catch (e) {
        debug('Discarding suspected cross-domain access error', e);
      }
    }

    return clients;
  }

  /**
   * Generates a proxy field, bound to a client
   *
   * @protected
   * @param {String} type field type to proxy
   * @param {Object} attributes proxy html attributes
   */
  createProxy (type, attributes = {}) {
    const { el } = this;
    const client = this.getClientByType(type);
    let proxy = window.document.createElement('input');

    attributes.type = attributes.type || 'text';
    attributes.style = 'position: absolute; bottom: -1000px; left: -1000px;';
    attributes.tabIndex = '-1';
    attributes['aria-hidden'] = true;

    Object.keys(attributes).forEach(attr => proxy.setAttribute(attr, attributes[attr]));

    debug(`creating proxy for ${type} deferred to`, client);

    // Bind proxy changes to client changes
    proxy.addEventListener('input', () => {
      debug(`${type} proxy change detected. updating deferred field to '${proxy.value}'`);
      client.el.value = proxy.value;
      // HACK: A bug in Safari for iOS prevents placeholders from
      //       clearing when values are changed programatically
      client.el.focus();
      client.el.dispatchEvent(createNativeEvent('focus'));
      el.focus();
    });

    el.parentNode.insertBefore(proxy, el.nextSibling);

    return proxy;
  }

  /**
   * Ensures that this.clientsReady will be set to true before creating proxies
   *
   * @param  {...any} args
   */
  createProxyAsync (...args) {
    setTimeout(() => {
      this.createProxy(...args);
    }, 0);
  }

  /**
   * Stores a list of client element ids
   *
   * called when an element is added or removed from an Elements
   * instance on the parent
   *
   * @private
   * @param {Array} options.ids ['uuid', 'uuid']
   */
  onPeerAnnounce ({ ids = [] } = {}) {
    this.clientElementIds = ids;
  }
};
