import * as validate from 'recurly.js/lib/recurly/validate';
import Cleave from 'cleave.js';
import Payment from 'payment';
import dom from 'recurly.js/lib/util/dom';
import wrapElement from '../util/wrap-element';
import { parseCard } from 'recurly.js/lib/util/parse-card';
import { Field } from './field';
import { ICONS } from './icons';
import { Required } from './concerns/required';
import { Tokenizing } from './concerns/tokenizing';
import { OPTIONS as MONTH_OPTIONS } from './month';
import { OPTIONS as YEAR_OPTIONS } from './year';
import cardStyleSheet from './card.css';

// Ignoring eslint because this regex correctly renders as `[0-9/\s]*` in html
// eslint-disable-next-line no-useless-escape
const NUMERIC_PATTERN = '[0-9\/\\s]*';
const NUMERIC_ATTRS = {
  type: 'tel',
  pattern: NUMERIC_PATTERN,
  spellcheck: 'false',
  autocapitalize: 'none',
  autocorrect: 'off'
};

export class CardField extends Required(Tokenizing(Field)) {
  constructor (...args) {
    super(...args);
  }

  get brand () {
    return validate.cardType(this.value, true);
  }

  get numberIsValid () {
    return validate.cardNumber(this.value);
  }

  get expiryisValid () {
    return Payment.fns.validateCardExpiry(this.expEl.value);
  }

  get cvvIsValid () {
    return validate.cvv(this.cvvEl.value);
  }

  get valid () {
    return this.numberIsValid && this.expiryisValid && this.cvvIsValid;
  }

  get numberIsInvalid () {
    if (this.value === '' || this.isFocused(this.el)) return false;
    return !this.numberIsValid;
  }

  get expiryisInvalid () {
    if (this.expEl.value === '' || this.isFocused(this.expEl)) return false;
    return !this.expiryisValid;
  }

  get cvvIsInvalid () {
    if (this.cvvEl.value === '' || this.isFocused(this.cvvEl)) return false;
    return !this.cvvIsValid;
  }

  // Custom default font size
  get fontSize () {
    const { config } = this;
    if (!config.touched.style || !config.touched.style.fontSize) return '16px';
    return config.config.style.fontSize;
  }

  inject (...args) {
    super.inject(...args);
    const { el } = this;

    el.setAttribute('autocomplete', 'cc-number');
    el.setAttribute('pattern', NUMERIC_PATTERN);
    el.className += ' recurly-hosted-field-input-number';

    this.injectContainer();
    this.expEl = createSecondaryInput(NUMERIC_ATTRS);
    this.cvvEl = createSecondaryInput(NUMERIC_ATTRS);

    const { expEl, cvvEl, container } = this;

    expEl.className = 'recurly-hosted-field-input recurly-hosted-field-input-expiry';
    cvvEl.className = 'recurly-hosted-field-input recurly-hosted-field-input-cvv';

    // Apply formatting
    new Cleave(el, { creditCard: true, creditCardStrictMode: true });
    Payment.formatCardExpiry(expEl);
    Payment.formatCardCVC(cvvEl);

    container.appendChild(expEl);
    container.appendChild(cvvEl);

    this.injectStyleSheet();
    this.injectExpirySelect();
  }

  injectContainer () {
    const { el } = this;
    const container = this.container = window.document.createElement('div');
    container.className = 'recurly-hosted-field-container';
    el.parentNode.appendChild(container);
    container.appendChild(el);
  }

  injectStyleSheet () {
    const constraintStyleSheet = this.constraintStyleSheet = window.document.createElement('style');
    constraintStyleSheet.innerHTML = cardStyleSheet.toString();
    window.document.head.appendChild(constraintStyleSheet);
  }

  /**
   * Injects an invisible proxy element atop the expiry field
   *
   * FIXME: Should this be live-configurable? Currently it requires re-initialization
   *
   * FIXME: There is too much code repetition between this method,
   *        injectMobileExpirySelect, and Select.inject.
   *        Rule of three applies. recurly.js/dom.createHiddenElement
   *        after v4.8.5 should allow these routines to be codensed
   *        into a util method `createInvisibleProxyElement`
   *        - Similar routines
   *          - select creation and style application
   *          - wrapper creation and injection
   *          - option creation
   *          - this.expEl unbindings
   *          - select proxy listener bindings
   */
  injectExpirySelect () {
    if (this.config.config.inputType === 'mobileSelect' && this.isMobile) {
      return this.injectMobileExpirySelect();
    }
    if (this.config.config.inputType !== 'select') return;

    // Creates proxy select inputs for month and year
    let expSelectMonthEl = window.document.createElement('select');
    let expSelectYearEl = window.document.createElement('select');
    [expSelectMonthEl, expSelectYearEl].forEach(proxyEl => {
      proxyEl.style.position = 'absolute';
      proxyEl.style.fontSize = this.fontSize;
      proxyEl.style.top = 0;
      proxyEl.style.width = '2em';
      proxyEl.style.height = '100%';
      proxyEl.style.opacity = 0;
    });
    expSelectMonthEl.style.left = 0;
    expSelectYearEl.style.left = '2em';

    // Wraps this.expEl and injects proxies
    const expElContainer = wrapElement(this.expEl);
    expElContainer.className = 'recurly-hosted-field-container-expiry';
    expElContainer.appendChild(expSelectMonthEl);
    expElContainer.appendChild(expSelectYearEl);

    // Builds option elements
    const injectOptions = target => value => {
      let option = window.document.createElement('option');
      option.value = option.text = value;
      target.appendChild(option);
    };
    MONTH_OPTIONS.forEach(injectOptions(expSelectMonthEl));
    YEAR_OPTIONS.forEach(injectOptions(expSelectYearEl));

    // Deactivates this.expEl
    this.expEl.tabIndex = -1;
    this.expEl.removeEventListener('focus', this.onFocus);
    this.expEl.addEventListener('focus', () => expSelectMonthEl.focus());

    // Activates new select proxy listeners
    const onChange = () => {
      const [month, year] = [expSelectMonthEl.value, expSelectYearEl.value];
      this.expEl.value = `${month} / ${year.substring(2)}`;
    };
    [expSelectMonthEl, expSelectYearEl].forEach(proxyEl => {
      proxyEl.addEventListener('keyup', this.onInput);
      proxyEl.addEventListener('focus', this.onFocus);
      proxyEl.addEventListener('change', onChange);
    });
  }

  injectMobileExpirySelect () {
    const minimumDate = new Date();
    const maximumDate = new Date();

    maximumDate.setFullYear(maximumDate.getFullYear() + 20);

    // Creates invisible proxy element
    let expSelectEl = dom.createHiddenInput({
      type: 'month',
      min: formatLimitDate(minimumDate),
      max: formatLimitDate(maximumDate),
    });
    expSelectEl.style.top = 0;
    expSelectEl.style.left = 0;
    expSelectEl.style.width = '100%';
    expSelectEl.style.height = '100%';

    // Wraps this.expEl and injects proxy
    const expElContainer = wrapElement(this.expEl);
    expElContainer.className = 'recurly-hosted-field-container-expiry';
    expElContainer.appendChild(expSelectEl);

    // Deactivates this.expEl
    this.expEl.tabIndex = -1;
    this.expEl.removeEventListener('focus', this.onFocus);
    this.expEl.addEventListener('focus', () => expSelectEl.focus());

    // Activates new select proxy listeners
    expSelectEl.addEventListener('keyup', this.onInput);
    expSelectEl.addEventListener('focus', this.onFocus);
    expSelectEl.addEventListener('change', e => {
      const [year, month] = e.target.value.split('-');
      this.expEl.value = month && year ? `${month} / ${year.substring(2)}` : '';
    });
  }

  bind () {
    super.bind();

    const { icon, el, expEl, cvvEl } = this;

    if (icon) icon.addEventListener('click', () => el.focus());

    el.addEventListener('focus', this.onNumberFocus.bind(this));
    el.addEventListener('blur', this.onNumberBlur.bind(this));

    expEl.addEventListener('keyup', this.onInput);
    expEl.addEventListener('focus', this.onFocus);

    cvvEl.addEventListener('keyup', this.onInput);
    cvvEl.addEventListener('focus', this.onFocus);
  }

  tabbing () {
    // Compatible only with Recurly.js 4.8.3+
    if (this.parentRecurlyJSVersionIsLessThan('4.8.3')) return;

    const { container, el, isMobile } = this;
    if (!isMobile) return;

    const previous = dom.createHiddenInput({ 'data-recurly-tabbing': 'prev' });
    const next = dom.createHiddenInput({ 'data-recurly-tabbing': 'next' });

    previous.addEventListener('focus', () => this.sendMessage('tab:previous'));
    next.addEventListener('focus', () => this.sendMessage('tab:next'));

    container.insertBefore(previous, el);
    container.appendChild(next);
  }

  update () {
    const { valid, brand, el, expEl, cvvEl, numberIsValid, expiryisValid, cvvIsValid } = this;
    const number = {
      empty: this.value === '',
      focus: this.isFocused(el),
      valid: numberIsValid
    };
    const expiry = {
      empty: expEl.value === '',
      focus: this.isFocused(expEl),
      valid: expiryisValid
    };
    const cvv = {
      empty: cvvEl.value === '',
      focus: this.isFocused(cvvEl),
      valid: cvvIsValid
    };

    const empty = number.empty && expiry.empty && cvv.empty;
    const focus = number.focus || expiry.focus || cvv.focus;

    let firstSix = '';
    let lastFour = '';

    const numberValue = parseCard(this.value);
    if (numberIsValid && numberValue.length >= 10) {
      firstSix = numberValue.substr(0, 6);
      lastFour = numberValue.substr(-4);
    }

    this.style();
    this.state = { valid, firstSix, lastFour, brand, empty, focus, number, expiry, cvv };
  }

  style () {
    const { config, el, expEl, cvvEl, numberIsInvalid, expiryisInvalid, cvvIsInvalid } = this;
    const { touched } = config;
    const { style } = config.config;

    // Custom defaults for card hosted fields
    const touchedStyle = touched.style || {};
    if (!touchedStyle.fontColor) style.fontColor = '#545457';
    if (!touchedStyle.fontFamily) style.fontFamily = '"Source Sans Pro", sans-serif';

    style.fontSize = this.fontSize;
    delete style.padding;

    if (!style.invalid.fontColor) style.invalid.fontColor = '#e35256';
    if (!style.placeholder.color) style.placeholder.color = '#a3a3a7';

    if (typeof style.placeholder.content !== 'object') style.placeholder.content = {};
    if (typeof style.placeholder.content.number !== 'string') style.placeholder.content.number = 'Card number';
    if (typeof style.placeholder.content.expiry !== 'string') style.placeholder.content.expiry = 'MM / YY';
    if (typeof style.placeholder.content.cvv !== 'string') style.placeholder.content.cvv = 'CVV';

    // Property translation
    style.invalid.color = style.invalid.fontColor;

    super.style();

    this.applyStyleToElement(expEl);
    this.applyStyleToElement(cvvEl);

    // Number element
    el.setAttribute('placeholder', style.placeholder.content.number);
    el.setAttribute('title', style.placeholder.content.number);
    el.setAttribute('aria-label', style.placeholder.content.number);

    // Expiry element
    expEl.setAttribute('placeholder', style.placeholder.content.expiry);
    expEl.setAttribute('title', style.placeholder.content.expiry);
    expEl.setAttribute('aria-label', style.placeholder.content.expiry);
    expEl.setAttribute('autocomplete', 'cc-exp');
    expEl.setAttribute('aria-required', true);

    // CVV element
    cvvEl.setAttribute('placeholder', style.placeholder.content.cvv);
    cvvEl.setAttribute('title', style.placeholder.content.cvv);
    cvvEl.setAttribute('aria-label', style.placeholder.content.cvv);
    cvvEl.setAttribute('autocomplete', 'cc-csc');
    cvvEl.setAttribute('aria-required', true);

    if (numberIsInvalid) Object.assign(el.style, style.invalid);
    if (expiryisInvalid) Object.assign(expEl.style, style.invalid);
    if (cvvIsInvalid) Object.assign(cvvEl.style, style.invalid);

    this.renderIcon();

    if (this.expEl.value.length > 7) {
      this.expEl.classList.add('recurly-hosted-field-input-expiry-long');
    } else {
      this.expEl.classList.remove('recurly-hosted-field-input-expiry-long');
    }
  }

  /**
   * Renders the card icon
   *  - Called continuously on field changes
   */
  renderIcon () {
    const { displayIcon } = this.config.config;

    if (!displayIcon) return this.removeIcon();
    if (!this.icon) this.injectIcon();

    const { brand, cvvEl, icon, numberIsValid } = this;
    let front = ICONS[brand] || (numberIsValid ? ICONS.genericValid : ICONS.generic);

    // Show a CVV hint icon if the cvv field is focused
    if (this.isFocused(cvvEl)) {
      if (brand === 'american_express') {
        front = ICONS.cvvFront;
      } else {
        icon.classList.add('recurly-hosted-field-icon-reversed');
      }
    } else {
      icon.classList.remove('recurly-hosted-field-icon-reversed');
    }

    // Replace the front icon
    if (this.iconFront === front) return;

    const start = () => {
      const time = parseFloat(window.getComputedStyle(icon).animationDuration) * 1000 / 2;
      setTimeout(() => {
        // Do nothing if the icon has not yet been added to the DOM.
        // This has only been observed in IE 11
        if (!icon.parentNode) return;
        icon.replaceChild(front, this.iconFront);
        this.iconFront = front;
      }, time);
    };
    const end = () => {
      icon.classList.remove('recurly-hosted-field-icon-replace');
      icon.removeEventListener('animationstart', start);
      icon.removeEventListener('animationend', end);
    };
    icon.addEventListener('animationstart', start);
    icon.addEventListener('animationend', end);
    icon.classList.add('recurly-hosted-field-icon-replace');
  }

  injectIcon () {
    const { el } = this;
    const iconHTML = `
      <div class="recurly-hosted-field-icon-container">
        <div class="recurly-hosted-field-icon">
          <div class="recurly-hosted-field-icon-front"></div>
          ${ICONS.cvvBack.outerHTML}
        </div>
      </div>
    `;
    el.insertAdjacentHTML('beforebegin', iconHTML);
    this.icon = el.parentNode.querySelector('.recurly-hosted-field-icon');
    this.iconFront = this.icon.querySelector('.recurly-hosted-field-icon-front');
  }

  removeIcon () {
    const { el, icon } = this;
    if (!icon) return;
    el.parentNode.removeChild(icon.parentNode);
    delete this.icon;
  }

  tokenPayload (body) {
    const { inputs } = body;
    const { el, cvvEl, expEl } = this;
    const { month, year } = Payment.fns.cardExpiryVal(expEl.value);

    inputs.number = el.value;
    inputs.month = month || '';
    inputs.year = year || '';
    inputs.cvv = cvvEl.value;

    return inputs;
  }

  onReady () {
    const { expEl, cvvEl } = this;
    expEl.style.visibility = 'visible';
    cvvEl.style.visibility = 'visible';
    super.onReady();
  }

  onNumberFocus () {
    const { container } = this;
    container.classList.add('recurly-hosted-field-isolate');
  }

  onNumberBlur () {
    const { container } = this;
    container.classList.remove('recurly-hosted-field-isolate');
  }
}

/**
 * Adds a leading zero to a Number if it is single-digit
 *
 * @param {Number} num
 * @return {Number}
 */
function addZ (num) {
  return ('0' + num).slice(-2);
}

/**
 * Formats a Date to be used as a min or max attribute on a MonthInputElement
 *
 * @param  {Date} date
 * @return {String} '2020-01'
 */
function formatLimitDate (date) {
  return `${date.getFullYear()}-${addZ(date.getMonth() + 1)}`;
}

/**
 * Creates a secondary input element
 *
 * @return {InputElement}
 * @private
 */
function createSecondaryInput (attributes = {}) {
  let el = window.document.createElement('input');
  if (!('type' in attributes)) attributes.type = 'text';
  Object.keys(attributes).forEach(attr => el.setAttribute(attr, attributes[attr]));
  return el;
}
