import { Field } from './field';
import { Select } from './concerns/select';
import { Required } from './concerns/required';
import { StrictNumeric } from './concerns/strict-numeric';
import { Connected } from './concerns/connected';

export const OPTIONS = ['', '01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'];

export class MonthField extends Required(Select(StrictNumeric(Connected(Field), { maxLength: 2 }))) {
  constructor (...args) {
    super(...args);
    this.el.setAttribute('autocomplete', 'cc-exp-month');
  }

  get options () {
    return OPTIONS;
  }

  get yearField () {
    return this.getClientByType('year');
  }

  get year () {
    let year = this.yearField.value;
    if (year.length === 2) {
      const prefix = currentYear().toString().slice(0, 2);
      year = prefix + year;
    }
    return year;
  }

  // Any year this year or later is valid
  get isValidYear () {
    return Boolean(this.year && this.year >= currentYear());
  }

  get isValidMonth () {
    const month = this.value;
    const isAnyValidMonth = ~this.options.indexOf(('0' + month).slice(-2));
    // If the selected year is the current year,
    // valid months are only later than the current month
    if (this.year === currentYear().toString()) {
      const currentMonth = (new Date).getMonth() + 1;
      return Boolean(month >= currentMonth && isAnyValidMonth);
    }
    // Otherwise, any month 1-12 is valid
    return Boolean(isAnyValidMonth);
  }

  update () {
    if (this.yearField) {
      this.yearField.state = { valid: this.isValidYear };
      this.state = { valid: this.isValidMonth };
    }
    super.update();
  }
}

/**
 * Gets the current year
 *
 * @return {Number} current 4 digit year
 */
function currentYear () {
  return (new Date).getFullYear();
}
