import Emitter from 'component-emitter';
import deepAssign from 'recurly.js/lib/util/deep-assign';

const DEFAULTS = {
  elementId: null,
  format: true,
  displayIcon: true,
  inputType: 'text', // 'mobileSelect', 'select'
  style: {
    fontColor: 'black',
    fontFamily: 'Helvetica',
    fontFeatureSettings: 'normal',
    fontKerning: 'auto',
    fontSize: 'inherit',
    fontSmoothing: 'auto',
    fontStretch: 'normal',
    fontStyle: 'normal',
    fontVariant: 'normal',
    fontWeight: 'normal',
    letterSpacing: 'normal',
    lineHeight: 'normal',
    padding: '',
    placeholder: {
      content: ''
    },
    textAlign: '',
    textDecoration: '',
    textRendering: 'auto',
    textShadow: 'none',
    textTransform: 'none',
    invalid: {}
  }
};

export class Config extends Emitter {
  constructor () {
    super();
    this.defaults = DEFAULTS;
    this.touched = {};
    this.set(Config.initialConfig);

    if (this.config.elementId) {
      this.on(`element:${this.config.elementId}:configure!`, (...args) => this.onConfigure(...args));
    } else {
      this.on('hostedField:configure', (...args) => this.onConfigure(...args));
    }
  }

  static get location () {
    return window.location;
  }

  /**
   * Gets initial configuration from the url. Default is from the hash,
   * and fallback is the querystring (deprecated behavior)
   */
  static get initialConfig () {
    const { location } = this;
    return parseMarshalledConfig(location.hash) || parseMarshalledConfig(location.search);
  }

  onConfigure (body) {
    if (body.type !== this.config.type) return;
    this.set(body);
  }

  set (options) {
    const configToSet = removeBlankKeys(options);
    const oldConfig = this.config;
    const newConfig = deepAssign({}, this.config || DEFAULTS, configToSet);
    if (JSON.stringify(oldConfig) !== JSON.stringify(newConfig)) {
      this.config = newConfig;
      // track which config values have been touched
      deepAssign(this.touched, objectValuesToTrue(configToSet));
      this.emit('change', this.config);
    }
  }
}

/**
 * Parses config out of a marshalled json string
 *
 * @param  {String} marshalledConfig
 * @return {Boolean} False if no marshalled content present
 * @return {Object} config object if marshalled content present
 */
function parseMarshalledConfig (marshalledConfig) {
  const offset = marshalledConfig.indexOf('config=');
  if (!~offset) return false;
  return JSON.parse(decodeURIComponent(marshalledConfig.substring(offset + 7)) || {});
}

function removeBlankKeys (obj) {
  obj = Object.assign({}, obj);
  Object.keys(obj).forEach(key => ~[undefined, null].indexOf(obj[key]) && delete obj[key]);
  return obj;
}

/**
 * Returns a cloned object whose non-object keys have been replaced with `true`
 *
 * @param  {Object} obj
 * @return {Object}
 */
function objectValuesToTrue (obj) {
  obj = Object.assign({}, obj);
  Object.keys(obj).forEach(key => {
    if (typeof obj[key] === 'object') obj[key] = objectValuesToTrue(obj[key]);
    else obj[key] = true;
  });
  return obj;
}
