import times from 'lodash.times';
import { Field } from './field';
import { Select } from './concerns/select';
import { Required } from './concerns/required';
import { StrictNumeric } from './concerns/strict-numeric';
import { Connected } from './concerns/connected';

export const OPTIONS = (() => {
  const year = (new Date()).getFullYear();
  return [''].concat(times(21, i => year + i));
})();

export class YearField extends Required(Select(StrictNumeric(Connected(Field), { maxLength: 4 }))) {
  constructor (...args) {
    super(...args);
    this.el.setAttribute('autocomplete', 'cc-exp-year');
  }

  get options () {
    return OPTIONS;
  }

  get monthField () {
    return this.getClientByType('month');
  }

  update () {
    if (this.monthField) {
      this.monthField.update();
    } else {
      this.state = { valid: false };
    }
    super.update();
  }
}
