import { Config } from './config';
import fieldFactory from './field/factory';

/**
 * Initializes a Field singleton for the frame
 *
 * @return {Field}
 */
export default function initialize () {
  console.log('RECURLY-HOSTED-FILED INITIALIZATION');
  const input = window.document.querySelector('input');
  const type = Config.initialConfig.type;

  if (Config.initialConfig.elementId) {
    window.name = `recurly-element:${Config.initialConfig.elementId}`;
  } else {
    window.name = `recurlyHostedField:${type}`;
  }

  return fieldFactory(type, input);
}
