import { Bus } from 'recurly.js/lib/recurly/bus';

/**
 * Bus variant which allows messages from any origin
 */
export class PermissiveBus extends Bus {
  originMatches () {
    return true;
  }
}

/**
 * Bus variant which is concerned only with sending messages
 */
export class SenderBus extends Bus {
  connect () {}
}
