import * as validate from 'recurly.js/lib/recurly/validate';
import Cleave from 'cleave.js';
import { Connected } from './concerns/connected';
import { Field } from './field';
import { parseCard } from 'recurly.js/lib/util/parse-card';
import { Required } from './concerns/required';
import { Tokenizing } from './concerns/tokenizing';

export class NumberField extends Required(Tokenizing(Connected(Field))) {
  constructor (...args) {
    super(...args);

    const { el, shouldFormat } = this;

    el.setAttribute('autocomplete', 'cc-number');
    if (shouldFormat) new Cleave(el, { creditCard: true, creditCardStrictMode: true });
  }

  update () {
    const value = parseCard(this.value);
    const valid = validate.cardNumber(value);
    const brand = validate.cardType(value, true);
    const length = value.length;
    const empty = this.value === '';
    const focus = this.isFocused();

    let firstSix = '';
    let lastFour = '';

    if (valid && length >= 10) {
      firstSix = value.substr(0, 6);
      lastFour = value.substr(-4);
    }

    this.state = { valid, firstSix, lastFour, brand, length, empty, focus };
  }

  tokenPayload (body) {
    const { inputs } = body;
    const { clients } = this;

    clients.forEach(({ type, value }) => inputs[type] = value);

    return inputs;
  }

  onReady () {
    super.onReady();
    this.createProxyAsync('month', { autocomplete: 'cc-exp-month' });
    this.createProxyAsync('year', { autocomplete: 'cc-exp-year' });
  }
}
