import amexIcon from './amex.svg';
import cvvBackIcon from './cvv-generic.svg';
import cvvFrontIcon from './cvv-amex.svg';
import dinersClubIcon from './diners-club.svg';
import discoverIcon from './discover.svg';
import eloIcon from './elo.svg';
import genericIcon from './generic.svg';
import genericValidIcon from './generic-valid.svg';
import hipercardIcon from './hipercard.svg';
import jcbIcon from './jcb.svg';
import mastercardIcon from './mastercard.svg';
import tarjetaNaranjaIcon from './tarjeta-naranja.svg';
import unionPayIcon from './union-pay.svg';
import visaIcon from './visa.svg';

const iconize = (str, side = 'front') => {
  let icon = window.document.createElement('div');
  icon.className = `recurly-hosted-field-icon-${side}`;
  icon.innerHTML = str;
  return icon;
};

export const ICONS = {
  american_express: iconize(amexIcon),
  cvvBack: iconize(cvvBackIcon, 'back'),
  cvvFront: iconize(cvvFrontIcon),
  diners_club: iconize(dinersClubIcon),
  discover: iconize(discoverIcon),
  elo: iconize(eloIcon),
  generic: iconize(genericIcon),
  genericValid: iconize(genericValidIcon),
  hipercard: iconize(hipercardIcon),
  jcb: iconize(jcbIcon),
  master: iconize(mastercardIcon),
  tarjeta_naranja: iconize(tarjetaNaranjaIcon),
  union_pay: iconize(unionPayIcon),
  visa: iconize(visaIcon)
};
